export const isbn13 = (str: string | Array<any> | any) => {

    if(str.length === 10){
        
        let count = 10;
        let str10= str.split("");
        str10[str10.length-1] === "X" ? str10[str10.length-1] =  10 :null;
        let convertedstr = str10.map((el:any) => el = parseInt(el))
        
        convertedstr.unshift(9,7,8)
        let checkDigit = convertedstr.map((el:any,index:any) => el *= index % 2 === 0 ?1:3 )
        console.log(checkDigit);
        
        
         convertedstr.map((el:any)=> el * count--).reduce((a:any,b:any)=>a + b) % 11 === 0 ?  "Valid":"Invalid";
    
            
    
    
        
    }else{
        str.split("")
    }
    
};


console.log(isbn13("0330301624"))
